.o-summary-minimal__content{overflow-y:hidden;padding:2px;left:-2px;top:-2px;position:relative}.o-summary-minimal__btn{margin-top:5px;padding-left:0;padding-right:0;display:block;border:none;background:none;text-align:left;color:var(--pacific)}.o-summary-minimal__btn:focus{outline:1px dotted var(--pacific);outline-offset:1px}.o-summary-minimal__btn .o-summary-minimal__cue-close,.o-summary-minimal__btn .o-summary-minimal__cue-open{display:none}.o-summary-minimal__btn[aria-expanded=false] .o-summary-minimal__cue-open{display:block}.o-summary-minimal__btn[aria-expanded=true] .o-summary-minimal__cue-close{display:block}.no-js .o-summary-minimal__btn{display:none}